import $ from "jquery";
import { AccumulationChart, AccumulationLegend, PieSeries, AccumulationTooltip, AccumulationDataLabel, } from "@syncfusion/ej2-charts";
import { Browser, registerLicense } from "@syncfusion/ej2/base";
AccumulationChart.Inject(AccumulationLegend, PieSeries, AccumulationTooltip, AccumulationDataLabel);
registerLicense("Ngo9BigBOggjHTQxAR8/V1NGaF1cVWhIfEx1RHxQdld5ZFRHallYTnNWUj0eQnxTdEZjUX5ecHNVRWNVVUV1Xw==");
const categoryBasedProductsCountArray = [];
const myData = [];
const mapData = (response) => {
    const totalProducts = response.reduce((total, item) => total + item.NumberOfProducts, 0);
    response.map((item) => {
        if (item.NumberOfProducts > 0) {
            myData.push({
                x: item.CategoryName,
                y: item.NumberOfProducts,
                text: `${item.CategoryName}: ${((item.NumberOfProducts / totalProducts) *
                    100).toFixed(2)}%`,
            });
        }
    });
    return myData;
};
$(document).ready(function () {
    // Perform an AJAX POST request as soon as the page loads
    $.ajax({
        url: "/product/GetCategoryBasedProductsCount",
        type: "POST",
        dataType: "json",
        success: function (response) {
            // Handle the response data here
            // console.log("AJAX response:", response);
            let pieChartFormattedData = mapData(response);
            console.log(pieChartFormattedData);
            let pie = new AccumulationChart({
                // Initialize the chart series
                series: [
                    {
                        dataSource: pieChartFormattedData,
                        dataLabel: {
                            visible: true,
                            position: "Outside",
                            name: "text",
                            font: { fontWeight: "300" },
                            connectorStyle: { length: "20px", type: "Curve" },
                        },
                        xName: "x",
                        yName: "y",
                        startAngle: Browser.isDevice ? 55 : 35,
                        innerRadius: "0%",
                        radius: Browser.isDevice ? "40%" : "70%",
                        explode: true,
                        explodeOffset: "10%",
                        explodeIndex: 0,
                        name: "Browser",
                    },
                ],
                center: { x: "50%", y: "50%" },
                enableSmartLabels: true,
                enableBorderOnMouseMove: false,
                enableAnimation: true,
                legendSettings: { visible: false },
                // Initialize tht tooltip
                tooltip: {
                    enable: true,
                    format: "<b>${point.x}</b><br>Stock Quantity : <b>${point.y}</b>",
                    header: "",
                },
                title: "Inventory Products Distribution by Category",
                titleStyle: {
                    fontWeight: "200",
                },
                load: (args) => {
                    let selectedTheme = location.hash.split("/")[1];
                    selectedTheme = selectedTheme ? selectedTheme : "Material";
                    args.accumulation.theme = ((selectedTheme.charAt(0).toUpperCase() + selectedTheme.slice(1))
                        .replace(/-dark/i, "Dark")
                        .replace(/contrast/i, "Contrast"));
                },
            });
            pie.appendTo("#container");
        },
        error: function (xhr, status, error) {
            // Handle errors here
            console.error("AJAX error:", error);
        },
    });
});
